import React, { Component } from "react";
import PropTypes from 'prop-types';
import { BigNumber } from 'bignumber.js'; // not web3.utils.BN
import NumericInput from 'react-numeric-input';

import sig2vrs from "utils/sig2vrs";
import api from "utils/api";
import hexToEvenLengthHex from "utils/hexToEvenLengthHex";

// import styles from "../styles/DelegatedTransferForm.module.css";

class DelegatedTransferForm extends Component {
  state = {
    delegate: "0xe8A098f3643Fd62C05A659F6b75f48562003b900",
    recipient: "",
    amount: 100, // SELF
    fee: new BigNumber(0),
    orderId: null,
    isDisabled: true,
    delegatedTransfer_api_url: "http://test.selftoken.co:8080/v1/delegatedTransfer",
  };

  componentDidMount = () => {
    const { queryParams, web3 } = this.props;
    console.log("DelegatedTransferForm queryParams", queryParams);

    if (!!queryParams.to) {
      if (web3.utils.isAddress(queryParams.to)) {
        console.log("DelegatedTransferForm: queryParams.to correctly parsed");
        this.setState({
          recipient: queryParams.to,
        })
      } else {
        console.warn("DelegatedTransferForm: queryParams.to is provided but not an address");
      }
    }

    if (!!queryParams.self) {
      const amount = parseFloat(queryParams.self);
      if (!isNaN(amount)) {
        console.log("DelegatedTransferForm: amount parsed, get", amount);
        this.setState({
          amount: amount,
        });
      } else {
        console.warn("DelegatedTransferForm: queryParams.amount is provided but parseFloat returns NaN.");
      }
    }

    // now only work on localhost
    if (window.location.hostname === "localhost" || window.location.hostname === "127.0.0.1") {
      this.setState({
        isDisabled: false,
      });
    } else {
      this.setState({
        isDisabled: true,
      });
    }
  }

  handleAmountChange = (value) => {
    this.setState({
      amount: value,
    });
  }

  handleRecipientChange = (event) => {
    this.setState({
      recipient: event.target.value,
    });
  }

  handleOrderIdChange = (value) => {
    this.setState({
      orderId: value,
    });
  }

  handleDelegatedTransferFormSubmit = async (event) => {
    event.preventDefault();

    const {
      web3,
      accounts,
      delegatedTransferOperator,
      networkId,
      callback
    } = this.props;

    const {
      recipient,
      amount, // number
      delegate,
      fee,
      isDisabled,
      delegatedTransfer_api_url,
    } = this.state;

    if (isDisabled) {
      return;
    }

    if (accounts.length === 0) {
      alert("No account injected form dapp browser.\nPlease unlock your account in MetaMask or the DApp browser.");
      return;
    }

    // check address and amount
    console.log("recipient", recipient);
    console.log("amount", amount);
    if (!web3.utils.isAddress(recipient)) {
      alert("The recipient address is not valid.");
      return;
    }

    let amountBN = new BigNumber(amount);
    let amountWeiBN = amountBN.times(1e18);

    const _operator = delegatedTransferOperator._address;
    const _to = recipient;
    // TODO: use server's wallet address
    const _delegate = delegate;
    const _value = amountWeiBN;
    const _fee = fee;
    const _nonce = Math.floor(Date.now() / 1000);
    // const _userDataNumber = this.state.orderId;
    // const _userData = hexToEvenLengthHex(web3.utils.numberToHex(_userDataNumber));
    const _userData = "0x";
    console.log("_userData", _userData);

    // delegationHash is a 32-byte ("0x" + 64 chars) hex string.
    const delegationHash = web3.utils.soliditySha3(
      {t: "address", v: _operator},
      {t: "address", v: _to},
      {t: "address", v: _delegate},
      {t: "uint256", v: _value},
      {t: "uint256", v: _fee},
      {t: "uint256", v: _nonce},
      {t: "bytes", v: _userData},
    );
    console.log("delegationHash", delegationHash);

    // delegationSignature is a 65-byte ("0x" + 130 chars) hex string.
    const _signature = await web3.eth.sign(delegationHash, accounts[0]);
    const _vrs = sig2vrs(_signature);
    const _int_v = parseInt(_vrs.v);

    if (_int_v !== 27 && _int_v !== 28) {
      throw new Error("_vrs.v not 27 or 28");
    }

    const version = 1;
    const operator_address = delegatedTransferOperator._address;
    console.log("operator_address", operator_address);

    const tx_data = delegatedTransferOperator.methods.transferPreSigned(
      _to,
      _delegate,
      _value,
      _fee,
      _nonce,
      _userData,
      // _signature
      _vrs.r,
      _vrs.s,
      _vrs.v
    // );
    ).encodeABI();
    console.log("tx_data", tx_data);

    const post_data = {
      networkId,
      version,
      delegatedTransferOperatorAddress: delegatedTransferOperator._address,
      tx_data,
    }

    api(delegatedTransfer_api_url, post_data, (order) => {
      // if transaction is broadcasted
      // data is order
      console.log("order", order);
      alert("交易已發送！\n取貨單號：" + order._id);
      callback();
    }, error => {
      // if failed
      console.error("error", error);
    });

    // axios.post(delegatedTransfer_api_url, post_data)
    // .then((response) => {
    //   console.log(response);
    // })
    // .catch((error) => {
    //   console.log(error);
    // });

    // .estimateGas({
    //   from: accounts[0],
    //   gas: 150000,
    // }).then((gasAmount) => {
    //   console.log("estimateGas gasAmount", gasAmount);
    // })
    // .catch((error) => {
    //   // revert or throw or any error
    //   console.error("estimateGas error", error);
    // });


    // delegatedTransferOperator.methods.transferPreSigned(
    //   _to,
    //   _delegate,
    //   _value,
    //   _fee,
    //   _nonce,
    //   _userData,
    //   // _signature
    //   _vrs.r,
    //   _vrs.s,
    //   _vrs.v
    // )
    // .send({
    //   from: accounts[0],
    // })
    // .once('transactionHash', (hash) => {
    //   console.log('once transactionHash', hash);
    // })
    // .once('error', (error) => {
    //   console.log('once error', error);
    // })
    // .then((receipt) => {
    //   // will be fired once the receipt is mined
    //   console.log("then receipt", receipt);
    //   callback();
    // });
  }

  render() {
    return (
      <form
        onSubmit={this.handleDelegatedTransferFormSubmit}
        id="transfer-form"
        className="transfer-form form-container"
      >
        <div className="form-group">
          <label htmlFor="delegatedTransferRecipientInput">
            Recipient
          </label>

          <input
            type="text"
            className=" address-input address-text"
            id="delegatedTransferRecipientInput"
            value={this.state.recipient}
            onChange={this.handleRecipientChange}
            placeholder="0x0000000000000000000000000000000000000000"
          />
        </div>

        <div className="form-group">
          <div className="text-center mb-3">
            <label htmlFor="transferAmount">Token Amount (SELF)</label>
            <NumericInput
              id="transferAmount"
              type="number"
              pattern="\d*"
              mobile
              className="numeric-input"
              min={0}
              step={10}
              value={this.state.amount}
              onChange={this.handleAmountChange}
            />
          </div>
        </div>
{/*
        <div className="form-group">
          <div className="text-center mb-3">
            <label htmlFor="orderId">Order ID</label>
            <NumericInput
              id="orderId"
              mobile
              className="numeric-input"
              min={0}
              step={1}
              value={this.state.orderId}
              onChange={this.handleOrderIdChange}
            />
          </div>
        </div> */}

        <div className="text-center">
          <button type="submit" className="solid-rectangle-button">
            {/* <span>Send</span> */}
            <span>Transfer</span>
          </button>
        </div>
      </form>
    );
  }
}

DelegatedTransferForm.propTypes = {
  web3: PropTypes.object,
  accounts: PropTypes.array,
  delegatedTransferOperator: PropTypes.object,
  networkId: PropTypes.number,
  callback: PropTypes.func,
};

DelegatedTransferForm.defaultProps = {
  web3: {},
  accounts: [],
  delegatedTransferOperator: {},
  networkId: 1,
  callback: () => {},
};

export default DelegatedTransferForm;
