// return vrs object
// v: hex string
// r: hex string
// s: hex string
const sig2vrs = (sig) => {
  if (typeof sig !== 'string') {
    throw new Error("in sig2vrs, signature must be type 'string', is currently type " + (typeof sig) + ".");
  }

  const stripped_sig = (sig.slice(0, 2) === '0x') ? sig.slice(2) : sig;

  if (stripped_sig.length !== 130) {
    throw new Error("sig length not 130 or 132 in sig2vrs");
  }

  const vrs = {
    v: "0x" + stripped_sig.slice(128, 130),
    r: "0x" + stripped_sig.slice(0, 64),
    s: "0x" + stripped_sig.slice(64, 128),
  }

  return vrs;
};

export default sig2vrs;
